* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a  {
    text-decoration: none;
    color: inherit;
}
li {
    list-style: none;
}
body {
    color: #444;
    background-color: lightgoldenrodyellow;
}
.header h1 {
    font-size: 50px;
    padding: 30px;
}
.header p {
    font-size: 20px;
    font-weight: 200;
    margin-bottom: 20px;
}
.check {
    max-width: 640px;
    width: calc(100% - 30px);
    margin:  0 auto;
}
.check form input[type="text"] {
    width: 100%;
    height:50px;
    border:3px solid #afdfff;
    border-radius: 10px;
    padding: 10px;
    font-size: 18px;
}
.check form {
    position: relative;
}
.check form button {
    position: absolute;
    top: 50%;
    right: -6px;
    transform: translate(0, -50%);
    background-color: transparent;
    border: 0;
}
.check form button img {    
    width: 100%;
    height: 60px;
    padding-top: 2px;
}

.check .check_list {
    width: 100%;
}

.check .check_list ul li{
    text-align: left;
    padding: 2% 15px;
    border-radius: 10px;
    border: 2px solid pink;
    margin: 10px 0;
    position:relative;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
}

.check .check_list ul li >ul {
    display: flex;
}
.check .check_list ul li >ul li {
    border: 0;
    padding: 0;
    margin: 0;
    color: lightgray;
    font-weight: 200;
}
.check .check_list ul li >ul li:nth-child(3){
    padding-right: 35px;
}
.check .check_list ul li button {
    background-color: transparent;
    border: 0;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translate(0, -50%);
    padding-top: 5px;
}